// src/components/Navbar.tsx
"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { Navbar as BSNav, Nav, Container, NavDropdown } from "react-bootstrap";
import { FaInfoCircle, FaCalendarAlt, FaServicestack, FaUser } from "react-icons/fa";

export default function Navbar() {
  const { data: session } = useSession();
  const router = useRouter();
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 50);
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  const handleReserva = (e) => {
    e.preventDefault();
    if (session) router.push("/reservas");
    else router.push("/login");
  };

  const servicios = [
    ["agua", "Estimulaci√≥n en agua"],
    ["piso", "Estimulaci√≥n en piso"],
    // ‚Ä¶ resto ‚Ä¶
  ];

  return (
    <BSNav sticky="top" expand="lg" className={scrolled ? "navbar-scrolled" : "navbar-base"}>
      <Container>
        <Link href="/" className="navbar-brand">
          <Image src="/images/logo_bloom_clean.png" width={105} height={105} alt="Bloom" />
        </Link>
        <BSNav.Toggle />
        <BSNav.Collapse>
          <Nav className="ms-auto align-items-center gap-3">
            <Link href="/nosotros" className="nav-link"><FaInfoCircle /> Nosotros</Link>
            <a href="#" onClick={handleReserva} className="nav-link"><FaCalendarAlt /> Citas</a>
            <NavDropdown title={<><FaServicestack/> Servicios</>} id="dd-serv">
              {servicios.map(([slug,label])=>(
                <a key={slug}
                   href="#"
                   onClick={(e)=>{
                     e.preventDefault();
                     if(session) router.push(`/reservas?service=${slug}`);
                     else router.push("/login");
                   }}
                   className="dropdown-item"
                >{label}</a>
              ))}
            </NavDropdown>
            {session 
              ? <a href="#" className="nav-link"><FaUser className="me-1"/> {session.user.name}</a>
              : <Link href="/login" className="nav-link">Iniciar sesi√≥n</Link>
            }
          </Nav>
        </BSNav.Collapse>
      </Container>
    </BSNav>
  );
}
